'use client';

import { useState, useMemo } from 'react';
import StatsCard from '@/components/StatsCard';
import SearchBar from '@/components/SearchBar';
import DataTable from '@/components/DataTable';
import styles from './DashboardClient.module.css';

export default function DashboardClient({ headers, rows, stats }) {
    const [searchTerm, setSearchTerm] = useState('');

    const filteredRows = useMemo(() => {
        if (!searchTerm) return rows;

        return rows.filter(row => {
            const searchLower = searchTerm.toLowerCase();
            return row.some(cell =>
                cell && cell.toString().toLowerCase().includes(searchLower)
            );
        });
    }, [rows, searchTerm]);

    return (
        <>
            {/* Stats Grid */}
            <div className={styles.statsGrid}>
                <StatsCard
                    label="Total Students"
                    value={stats.total}
                    icon="ðŸ‘¥"
                    color="blue"
                />
                <StatsCard
                    label="Approved"
                    value={stats.approved}
                    icon="âœ“"
                    color="green"
                />
                <StatsCard
                    label="Pending"
                    value={stats.pending}
                    icon="â³"
                    color="orange"
                />
                <StatsCard
                    label="Not Submitted"
                    value={stats.notSubmitted}
                    icon="âœ—"
                    color="red"
                />
            </div>

            {/* Search Bar */}
            <div className={styles.searchSection}>
                <SearchBar
                    onSearch={setSearchTerm}
                    placeholder="Search by name or registration number..."
                />
                <div className={styles.resultCount}>
                    Showing {filteredRows.length} of {rows.length} students
                </div>
            </div>

            {/* Data Table */}
            <DataTable headers={headers} rows={filteredRows} />
        </>
    );
}
